import { ScrollingModule } from '@angular/cdk/scrolling';
import { IonicModule } from '@ionic/angular';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DestroyProductComponent } from './destroy-product.component';
import { EditComponent } from './edit/edit.component';
import { DetailComponent } from './detail/detail.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { SearchModule } from 'src/app/cafe/components/search/search.module';
import { BillType38Module } from 'src/app/cafe/components/bill-type/bill-type-38/bill-type-38.module';

const routes: Routes = [
  {
    path: '',
    component: DestroyProductComponent,
  },
  {
    path: 'detail',
    component: DetailComponent,
  },
  {
    path: 'edit',
    component: EditComponent,
  },
];

@NgModule({
  declarations: [
    DestroyProductComponent,
    EditComponent,
    DetailComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    ReactiveFormsModule,
    FormsModule,
    ScrollingModule,
    BillType38Module,
  ],
})
export class DestroyProductModule {}
